%avatar-base {
  display: block;
  margin: 1px;
  overflow: hidden;
  border-radius: 50%;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.moreUser {
  position: relative;

  .is-active {
    @include flex;
    @include pos-center;
    top: 4px;
    left: 28px;
    width: 13px;
    height: 13px;
    background-color: $blue;
    border-radius: 5px;
  }
}

.avatarBorder {
  @include flex;
  width: 32px;
  height: 32px;
  border: 1px solid $border;
  border-radius: 50%;
  overflow: hidden;
}

.avatarDefault {
  @extend %avatar-base;
  width: 28px;
  height: 28px;
  margin: 1px;
}
